@media (min-width: 0) {
  .landing-wrapper {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-perspective: 2px;
            perspective: 2px; }
  .landing {
    position: relative;
    width: 100vw;
    height: 100vh;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    border-top: 8px solid #598e89;
    div-margin: 15px;
    div-text-align: left;
    div-display: flex;
    div-flex-direction: column;
    div-justify-content: flex-end;
    div-align-items: center; }
  .parallax::after {
    /* Display and position the pseudo-element */
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    /* Move the pseudo-element back away from the camera,
     * then scale it back up to fill the viewport.
     * Because the pseudo-element is further away, it appears to move more slowly, like in real life. */
    -webkit-transform: translateZ(-1px) scale(1.5);
            transform: translateZ(-1px) scale(1.5);
    /* Force the background image to fill the whole element. */
    /* Keep the image from overlapping sibling elements. */
    z-index: -1; }
  .landing::after {
    background-image: url("https://images.unsplash.com/photo-1532938911079-1b06ac7ceec7?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1489&q=80");
    background-size: cover; }
  .logo-top {
    font-size: 1.8rem;
    color: black;
    font-family: "Big Shoulders Text", cursive;
    position: absolute;
    right: 2%;
    top: 2%; }
  .title {
    color: black;
    font-size: 3.5rem;
    width: 330px;
    padding-left: 20px;
    text-align: left;
    font-weight: bold; }
  .button-container {
    display: flex;
    justify-content: space-evenly;
    padding: 20px;
    margin-bottom: 55px; }
  .home-btn {
    width: 140px;
    margin: 0 10px 10px 10px; }
  .arrow {
    position: absolute;
    left: 50%;
    top: 94%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .arrow span {
    display: block;
    justify-content: center;
    width: 20px;
    height: 20px;
    border-bottom: 5px solid white;
    border-right: 5px solid white;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    margin: -10px;
    -webkit-animation: animate 2s 3;
            animation: animate 2s 3; }
  .arrow span:nth-child(2) {
    -webkit-animation-delay: -0.2s;
            animation-delay: -0.2s; }
  @-webkit-keyframes animate {
    0% {
      opacity: 0;
      -webkit-transform: rotate(45deg) translate(-8px, -8px);
              transform: rotate(45deg) translate(-8px, -8px); }
    50% {
      opacity: 1; }
    100% {
      opacity: 0;
      -webkit-transform: rotate(45deg) translate(8px, 8px);
              transform: rotate(45deg) translate(8px, 8px); } }
  @keyframes animate {
    0% {
      opacity: 0;
      -webkit-transform: rotate(45deg) translate(-8px, -8px);
              transform: rotate(45deg) translate(-8px, -8px); }
    50% {
      opacity: 1; }
    100% {
      opacity: 0;
      -webkit-transform: rotate(45deg) translate(8px, 8px);
              transform: rotate(45deg) translate(8px, 8px); } }
